










import {
    Component, Provide, Prop, Vue
} from 'vue-property-decorator';

@Component
export default class HomeTab extends Vue {
    @Prop() private msg!: string;
    @Provide() tabs: Array<Object> = [
        {
            'id':0,
            'title':'首页',
        },
        {
            'id':1,
            'title':'编程',
        },
        {
            'id':2,
            'title':'读书',
        },
        {
            'id':3,
            'title':'旅游',
        },
        {
            'id':4,
            'title':'运动',
        }
    ];

    goTabPage(id:number) {
        if (id === 0) {
            this.$router.push('/');
        } else {
            this.$router.push(`/category/${id}`);
        }
    }
}
